import React from "react";
import "./SectionTitle.css";

export default class SectionTitle extends React.Component {
   constructor(props) {
      super(props);
      this.myRef = React.createRef();
      this.state = {
         animationClass: "",
      };
      this.isVisible = this.isVisible.bind(this);
   }
   componentDidMount() {
      window.addEventListener("scroll", this.isVisible);
   }
   componentWillUnmount() {
      window.removeEventListener("scroll", this.isVisible);
   }

   isVisible() {
      if (!this.myRef.current) return;
      if (
         this.myRef.current.offsetTop <=
         window.pageYOffset + (window.innerHeight * 2) / 3
      ) {
         this.setState({
            animationClass: this.props.left
               ? " animated-slide-in-left"
               : " animated-slide-in-right",
         });
         window.removeEventListener("scroll", this.isVisible);
      }
   }

   render() {
      return (
         <div ref={this.myRef} className={"title-wrapper"}>
            <h1 className={"section-title" + this.state.animationClass}>
               {this.props.name.toUpperCase()}
            </h1>
            <div className={"underscore" + this.state.animationClass}></div>
         </div>
      );
   }
}
