import React from "react";
import "./About.css";
import SectionTitle from "./SectionTitle";
import "./SectionTitle.css";

export default class About extends React.Component {
   constructor(props) {
      super(props);
      this.desc = [
         "I'm Vlad Ciocoiu, I'm 17 and I'm a beginner software developer. " +
            "I like programming in Python and C++, but I mainly use Javascript.",
         "I'm really into Math and Computer Science " +
            "(especially algorithms and data structures).",
         "Currently I'm learning front-end web development, using React.js. " +
            "I really like animations, interactive elements (as you can see) " +
            "and I always try to create responsive designs for my websites.",
      ];
      this.imgUrl = "./images/about/me.JPG";
   }

   render() {
      return (
         <div className="about">
            <SectionTitle name="about" left={true} />
            <DisplayImg url={this.imgUrl} index={0} />
            {this.desc.map((string, index) => (
               <DisplayText
                  text={string}
                  index={index}
                  left={!(index % 2)}
                  key={index}
               />
            ))}
         </div>
      );
   }
}

class DisplayText extends React.Component {
   constructor(props) {
      super(props);
      this.myRef = React.createRef();
      this.state = {
         animationClass: "",
      };
      this.isVisible = this.isVisible.bind(this);
   }
   componentDidMount() {
      window.addEventListener("scroll", this.isVisible);
   }
   componentWillUnmount() {
      window.removeEventListener("scroll", this.isVisible);
   }

   isVisible() {
      if (!this.myRef.current) return;
      if (
         this.myRef.current.offsetTop <=
         window.pageYOffset + (window.innerHeight * 2) / 3
      ) {
         this.setState({
            animationClass: this.props.left
               ? " animated-slide-in-left"
               : " animated-slide-in-right",
         });
         window.removeEventListener("scroll", this.isVisible);
      }
   }

   render() {
      return (
         <h2
            ref={this.myRef}
            className={"text-" + this.props.index + this.state.animationClass}
         >
            {this.props.text}
         </h2>
      );
   }
}

class DisplayImg extends React.Component {
   constructor(props) {
      super(props);
      this.myRef = React.createRef();
      this.state = {
         animationClass: "",
      };
      this.isVisible = this.isVisible.bind(this);
   }
   componentDidMount() {
      window.addEventListener("scroll", this.isVisible);
   }
   componentWillUnmount() {
      window.removeEventListener("scroll", this.isVisible);
   }

   isVisible() {
      if (!this.myRef.current) return;
      if (
         this.myRef.current.offsetTop <=
         window.pageYOffset + (window.innerHeight * 2) / 3
      ) {
         this.setState({
            animationClass: " photo-animated",
         });
         window.removeEventListener("scroll", this.isVisible);
      }
   }

   render() {
      return (
         <img
            ref={this.myRef}
            className={"image-" + this.props.index + this.state.animationClass}
            src={this.props.url}
            alt=""
         />
      );
   }
}
