import React from "react";
import "./Navbar.css";

export default class Navbar extends React.Component {
   constructor(props) {
      super(props);
      this.updateSidebar = this.updateSidebar.bind(this);
      this.state = {
         items: ["About", "Projects"],
         sidebarActive: null,
      };
   }

   updateSidebar() {
      if (this.state.sidebarActive === null)
         this.setState({ sidebarActive: true });
      else this.setState({ sidebarActive: !this.state.sidebarActive });
   }

   render() {
      ///doing this to prevent the element from animating when the page is loaded
      let overlayClass = "";
      if (this.state.sidebarActive) overlayClass = "black-active";
      else if (this.state.sidebarActive === false) overlayClass = "black-rev";
      let navLinksClass = "";
      if (this.state.sidebarActive) navLinksClass = "nav-active";
      else if (this.state.sidebarActive === false) navLinksClass = "nav-rev";

      return (
         <>
            <div className={"black-overlay " + overlayClass}></div>
            <nav>
               <div className="logo">
                  <h1>Vlad Ciocoiu</h1>
               </div>
               <ul className={"nav-links " + navLinksClass}>
                  {this.state.items.map((item, index) => (
                     <ListItem key={item} value={item} />
                  ))}
               </ul>
               <BurgerMenu
                  active={this.state.sidebarActive}
                  update={this.updateSidebar}
               />
            </nav>
         </>
      );
   }
}

class ListItem extends React.Component {
   constructor(props) {
      super(props);
      this.handleHover = this.handleHover.bind(this);
      this.handleClick = this.handleClick.bind(this);
      this.state = {
         hovered: null,
      };
   }

   handleHover() {
      this.setState({
         hovered: this.state.hovered == null ? true : !this.state.hovered,
      });
   }

   // maybe it's considered bad practice to access the DOM directly but I find it
   // easier to do this than to use context
   handleClick() {
      const section = document.querySelector(
         "." + this.props.value.toLowerCase()
      );
      section.scrollIntoView(true);
   }

   render() {
      let thisClass = "";
      if (this.state.hovered === true) thisClass = "nav-link-hover";
      else if (this.state.hovered === false) thisClass = "nav-link-rev";

      return (
         <button
            className={thisClass}
            onMouseOver={this.handleHover}
            onMouseOut={this.handleHover}
            onClick={this.handleClick}
         >
            {this.props.value}
         </button>
      );
   }
}

class BurgerMenu extends React.Component {
   constructor(props) {
      super(props);
      this.handleClick = this.handleClick.bind(this);
      this.buttonLocked = false;
      this.state = {
         visible: false,
      };
   }

   componentDidMount() {
      window.addEventListener("scroll", () => {
         if (
            window.pageYOffset > window.innerHeight &&
            window.innerWidth < 990
         ) {
            this.setState({ visible: true });
         } else {
            this.setState({ visible: false });
            if (this.props.active) this.props.update();
         }
      });
   }

   handleClick() {
      if (this.buttonLocked) return;

      ///lock the button to prevent spamming
      this.buttonLocked = true;
      setTimeout(() => (this.buttonLocked = false), 500);
      this.props.update();
   }

   render() {
      let lineClass = "";
      if (this.props.active) lineClass = "animate";
      else if (this.props.active === false) lineClass = "animate-rev";
      const burgerClass = this.state.visible ? "visible" : "hidden";

      return (
         <div
            className={"burger-menu " + burgerClass}
            onClick={this.handleClick}
         >
            <span className={"line-1 " + lineClass}></span>
            <span className={"line-2 " + lineClass}></span>
            <span className={"line-3 " + lineClass}></span>
         </div>
      );
   }
}
