import React from "react";
import "./Home.css";
import Icon from "@mdi/react";
import { mdiArrowRight } from "@mdi/js";

export default class Home extends React.Component {
   constructor(props) {
      super(props);
      this.handleHover = this.handleHover.bind(this);
      this.handleButtonPress = this.handleButtonPress.bind(this);
      this.state = {
         buttonIsHovered: null,
      };
   }

   handleButtonPress() {
      document.body.scrollTop = window.innerHeight;
      document.documentElement.scrollTop = window.innerHeight;
   }

   handleHover() {
      this.setState({
         buttonIsHovered:
            this.state.buttonIsHovered == null
               ? true
               : !this.state.buttonIsHovered,
      });
   }

   render() {
      let arrowClass = "";
      let buttonClass = "home-button ";
      if (this.state.buttonIsHovered === true) {
         arrowClass = "arrow-over";
         buttonClass += "home-button-hovered";
      } else if (this.state.buttonIsHovered === false) {
         arrowClass = "arrow-out";
         buttonClass += "home-button-rev";
      }

      return (
         <div className="home">
            <h2>
               Hi, I'm <i className="home-name">Vlad Ciocoiu</i>.
            </h2>
            <h2>I create software.</h2>
            <button
               className={buttonClass}
               onMouseOver={this.handleHover}
               onMouseOut={this.handleHover}
               onClick={this.handleButtonPress}
            >
               Find out more{" "}
               <Icon
                  path={mdiArrowRight}
                  size={1}
                  className={"arrow " + arrowClass}
               />
            </button>
         </div>
      );
   }
}
